"use client";

import { useState, FormEvent, useEffect } from "react";
import { useRouter, useSearchParams } from "next/navigation";
import Button from "@/components/ui/Button";
import Input from "@/components/ui/Input";
import Card from "@/components/ui/Card";
import { Sparkles, Mail, ArrowLeft, CheckCircle } from "lucide-react";
import Link from "next/link";

export default function VerifyEmailPage() {
  const router = useRouter();
  const searchParams = useSearchParams();
  const email = searchParams.get("email") || "";
  
  const [isLoading, setIsLoading] = useState(false);
  const [code, setCode] = useState("");
  const [localError, setLocalError] = useState<string | null>(null);
  const [success, setSuccess] = useState(false);
  const [resendSuccess, setResendSuccess] = useState(false);
  const [showCode, setShowCode] = useState(false);

  useEffect(() => {
    if (!email) {
      router.push("/register");
      return;
    }
    
    // Sayfa yÃ¼klendiÄŸinde localStorage'dan kodu kontrol et ve gÃ¶ster
    const verificationData = localStorage.getItem("email-verification");
    if (verificationData) {
      try {
        const data = JSON.parse(verificationData);
        if (data.email === email && Date.now() <= data.expiresAt) {
          // Kod hala geÃ§erli, gÃ¶ster
          setShowCode(true);
        }
      } catch (err) {
        console.error("Error parsing verification data:", err);
      }
    }
  }, [email, router]);

  const handleSubmit = async (e: FormEvent) => {
    e.preventDefault();
    setIsLoading(true);
    setLocalError(null);
    setResendSuccess(false);

    try {
      // Validation
      if (!code) {
        setLocalError("DoÄŸrulama kodu gereklidir");
        setIsLoading(false);
        return;
      }

      if (code.length !== 6) {
        setLocalError("DoÄŸrulama kodu 6 haneli olmalÄ±dÄ±r");
        setIsLoading(false);
        return;
      }

      // Frontend'de doÄŸrulama kodu kontrolÃ¼
      const verificationData = localStorage.getItem("email-verification");
      
      if (!verificationData) {
        setLocalError("DoÄŸrulama kodu bulunamadÄ±. LÃ¼tfen kodu tekrar gÃ¶nderin.");
        setIsLoading(false);
        return;
      }

      const data = JSON.parse(verificationData);
      
      // Email kontrolÃ¼
      if (data.email !== email) {
        setLocalError("Email adresi eÅŸleÅŸmiyor.");
        setIsLoading(false);
        return;
      }
      
      // SÃ¼re kontrolÃ¼
      if (Date.now() > data.expiresAt) {
        setLocalError("DoÄŸrulama kodu sÃ¼resi dolmuÅŸ. LÃ¼tfen kodu tekrar gÃ¶nderin.");
        localStorage.removeItem("email-verification");
        setShowCode(false);
        setIsLoading(false);
        return;
      }
      
      // Kod kontrolÃ¼
      if (data.code !== code) {
        setLocalError("DoÄŸrulama kodu hatalÄ±. LÃ¼tfen tekrar deneyin.");
        setIsLoading(false);
        return;
      }
      
      // Kod doÄŸru, localStorage'dan sil
      localStorage.removeItem("email-verification");
      setShowCode(false);
      
      console.log("âœ… Email doÄŸrulama baÅŸarÄ±lÄ±!");
      setSuccess(true);
      setIsLoading(false);
      
      // 2 saniye sonra login sayfasÄ±na yÃ¶nlendir
      setTimeout(() => {
        router.push("/login");
      }, 2000);
    } catch (err) {
      console.error("Verification error:", err);
      setLocalError("DoÄŸrulama iÅŸlemi sÄ±rasÄ±nda bir hata oluÅŸtu. LÃ¼tfen tekrar deneyin.");
      setIsLoading(false);
    }
  };

  const handleResend = () => {
    setLocalError(null);
    setResendSuccess(false);

    // Frontend'de yeni kod oluÅŸtur
    const verificationCode = Math.floor(100000 + Math.random() * 900000).toString();
    
    const verificationData = {
      email: email,
      code: verificationCode,
      expiresAt: Date.now() + 5 * 60 * 1000, // 5 dakika
    };
    localStorage.setItem("email-verification", JSON.stringify(verificationData));
    
    console.log("âœ… Yeni doÄŸrulama kodu oluÅŸturuldu.");
    console.log("ðŸ”‘ Yeni DoÄŸrulama Kodu (TEST):", verificationCode);
    console.log("ðŸ’¡ Bu kod sadece test iÃ§in gÃ¶steriliyor. Production'da email ile gÃ¶nderilecek.");
    
    setResendSuccess(true);
    setTimeout(() => {
      setResendSuccess(false);
    }, 5000);
  };

  const displayError = localError;

  if (success) {
    return (
      <div className="min-h-screen bg-dark-950 flex items-center justify-center p-4">
        <div className="w-full max-w-md">
          <Card className="p-8 text-center">
            <div className="inline-flex items-center justify-center w-16 h-16 bg-green-600/20 rounded-full mb-4">
              <CheckCircle className="w-8 h-8 text-green-400" />
            </div>
            <h2 className="text-2xl font-bold text-foreground mb-2">Email DoÄŸrulandÄ±!</h2>
            <p className="text-muted-foreground mb-6">
              HesabÄ±nÄ±z baÅŸarÄ±yla aktifleÅŸtirildi. GiriÅŸ sayfasÄ±na yÃ¶nlendiriliyorsunuz...
            </p>
            <Button
              onClick={() => router.push("/login")}
              className="w-full"
            >
              GiriÅŸ SayfasÄ±na Git
            </Button>
          </Card>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-dark-950 flex items-center justify-center p-4">
      <div className="w-full max-w-md">
        {/* Logo & Title */}
        <div className="text-center mb-8">
          <div className="inline-flex items-center justify-center w-16 h-16 bg-gradient-to-br from-primary-500 to-primary-700 rounded-2xl mb-4">
            <Sparkles className="w-8 h-8 text-white" />
          </div>
          <h1 className="text-3xl font-bold text-foreground mb-2">Email DoÄŸrulama</h1>
          <p className="text-muted-foreground">
            <strong>{email}</strong> adresine gÃ¶nderilen doÄŸrulama kodunu giriniz
          </p>
        </div>

        {/* Verify Card */}
        <Card className="p-8">
          <form onSubmit={handleSubmit} className="space-y-6">
            {/* Error Message */}
            {displayError && (
              <div className="p-4 bg-red-600/10 border border-red-600/30 rounded-lg">
                <p className="text-red-400 text-sm">{displayError}</p>
              </div>
            )}

            {/* Success Message */}
            {resendSuccess && (
              <div className="p-4 bg-green-600/10 border border-green-600/30 rounded-lg">
                <p className="text-green-400 text-sm">DoÄŸrulama kodu tekrar gÃ¶nderildi!</p>
              </div>
            )}

            {/* Code Field */}
            <div className="space-y-2">
              <label htmlFor="code" className="block text-sm font-medium text-foreground">
                DoÄŸrulama Kodu <span className="text-red-400">*</span>
              </label>
              <div className="relative">
                <Mail className="absolute left-3 top-1/2 transform -translate-y-1/2 w-5 h-5 text-muted-foreground" />
                <Input
                  id="code"
                  type="text"
                  value={code}
                  onChange={(e) => {
                    // Sadece rakamlarÄ± kabul et ve maksimum 6 karakter
                    const value = e.target.value.replace(/\D/g, "").slice(0, 6);
                    setCode(value);
                  }}
                  placeholder="123456"
                  className="pl-10 text-center text-2xl tracking-widest"
                  disabled={isLoading}
                  required
                  maxLength={6}
                  autoComplete="one-time-code"
                />
              </div>
              <p className="text-xs text-muted-foreground">
                6 haneli doÄŸrulama kodunu giriniz
              </p>
              
              {/* Test iÃ§in kod gÃ¶ster (sadece development) */}
              {showCode && (
                <div className="p-3 bg-primary-600/10 border border-primary-600/30 rounded-lg">
                  <p className="text-xs text-primary-400 mb-1">ðŸ’¡ Test iÃ§in doÄŸrulama kodu:</p>
                  <p className="text-lg font-mono font-bold text-primary-300 text-center">
                    {(() => {
                      try {
                        const data = JSON.parse(localStorage.getItem("email-verification") || "{}");
                        return data.code || "BulunamadÄ±";
                      } catch {
                        return "BulunamadÄ±";
                      }
                    })()}
                  </p>
                  <p className="text-xs text-muted-foreground mt-2 text-center">
                    (Bu kod sadece test iÃ§in gÃ¶steriliyor)
                  </p>
                </div>
              )}
            </div>

            {/* Submit Button */}
            <Button
              type="submit"
              className="w-full"
              disabled={isLoading || code.length !== 6}
            >
              {isLoading ? "DoÄŸrulanÄ±yor..." : "DoÄŸrula"}
            </Button>

            {/* Resend Code */}
            <div className="text-center">
              <button
                type="button"
                onClick={handleResend}
                disabled={isLoading}
                className="text-sm text-primary-400 hover:text-primary-300 disabled:opacity-50 disabled:cursor-not-allowed"
              >
                Kodu tekrar gÃ¶nder
              </button>
            </div>

            {/* Back to Register */}
            <div className="pt-4 border-t border-dark-800">
              <Link
                href="/register"
                className="flex items-center justify-center gap-2 text-sm text-muted-foreground hover:text-foreground transition-colors"
              >
                <ArrowLeft className="w-4 h-4" />
                KayÄ±t sayfasÄ±na dÃ¶n
              </Link>
            </div>
          </form>
        </Card>
      </div>
    </div>
  );
}

